'use client';
import Link from 'next/link';

export default function ProductCard({ product }) {
    const BASE_URL = "http://localhost:1337"
    const firstImageUrl = product.images?.[0]?.url
        ? `${BASE_URL}${product.images[0].url}`
        : '/placeholder.png';

    return (
        <Link href={`/products/${product.slug}`} className="block border rounded-md overflow-hidden shadow hover:shadow-lg transition">
            <img
                src={firstImageUrl}
                alt={product.title}
                className="w-full h-64 object-cover"
            />
            <div className="p-4">
                <h2 className="text-lg font-semibold">{product.title}</h2>
                <p className="text-gray-500 line-through">Rs. {product.oldPrice}</p>
                <p className="text-red-600 font-bold">Rs. {product.price}</p>
            </div>
        </Link>
    );
}